import { Component, ViewChild, OnInit } from '@angular/core';

import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { Table } from '../Schema/Table';
import { TableService } from '../Service/Table.service';
import { MatSort } from '@angular/material/sort';
import { Location } from '@angular/common';


/**
 * @title Table with pagination
 */

@Component({
  selector: 'app-pagination',
  templateUrl: './pagination.component.html',
  styleUrls: ['./pagination.component.css']
})
export class PaginationComponent implements OnInit {

  contentArray: Table[];
  columnName: string[] = [
    'mal_id',
    'title',
    'airing',
    'type',
    'episodes',
    'score',
    'start_date',
    'end_date',
    'members',
    'rated'];
  dataSource = new MatTableDataSource<Table>(this.contentArray);
 
  constructor(private _tableService: TableService, private _location: Location) { }

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;


  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  ngOnInit() {
    this.getData();
  }


  public getData() {
    let response = this._tableService.getTableData();
    response.subscribe(resData =>
      this.dataSource.data = resData as Table[]
    )
  }

  backCLicked() {
    this._location.back();
  }
}

