import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable, throwError } from 'rxjs';
import { Injectable } from "@angular/core";
import { Table } from '../Schema/Table';

import {map, catchError} from 'rxjs/operators';

@Injectable()

export class TableService {
    private get_url="https://api.jikan.moe/v3/search/anime?q=naruto";
    httpOptions={
        headers : new HttpHeaders({ 'Content-Type' : 'Applicaton/json' })
    }


constructor(private _httpClient : HttpClient){}

getTableData() : Observable<Table[]>{

    return this._httpClient.get<Table[]>(this.get_url)
    .pipe(map((resData: any) => resData.results ),    
    catchError(error => { return throwError('Check again')}));
}}