import { Table } from 'src/app/Schema/Table';
import { Location } from '@angular/common';

import { TableService } from 'src/app/Service/Table.service';
import { AfterViewInit, Component, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';

/**
 * @title Table with pagination
 */
@Component({
  selector: 'app-scrollable',
  templateUrl: './scrollable.component.html',
  styleUrls: ['./scrollable.component.css']
})
export class ScrollableComponent implements AfterViewInit {
  columnName: string[] = [
    'mal_id',
    'title',
    'airing',
    'type',
    'episodes',
    'score',
    'start_date',
    'end_date',
    'members',
    'rated'];
  contentArray: Table[];
  dataSource = new MatTableDataSource<Table>(this.contentArray);


  constructor(private _tableService: TableService, private _location: Location) { }

  @ViewChild(MatSort, { static: false }) sort: MatSort;

  ngAfterViewInit() {
    this.dataSource.sort = this.sort;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  ngOnInit() {
    this.getData();
  }


  public getData() {
    let response = this._tableService.getTableData();
    response.subscribe(resData =>
      this.dataSource.data = resData as Table[]
    )
  }

  backCLicked() {
    this._location.back()
  }
}



